.page {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #020c18;
  font-family: "Inter", Arial, sans-serif;
  min-height: 100vh;
  max-width: 100%;
}

.link-dissolution {
  transition: opacity 0.3s ease-in-out;
}

.link-dissolution:hover {
  opacity: 0.5;
}
