.main {
  color: #fff;
  width: 100%;
  padding: 0 80px;
  box-sizing: border-box;
}

.section {
  padding: 40px 0;
}

.section__title {
  font-family: "JetBrains Mono", "Inter", sans-serif;
  font-weight: 700;
  font-size: 32px;
  line-height: 42px;
  margin: 0 0 56px 0;
}

@media screen and (max-width: 500px) {
  .main {
    padding: 0 30px;
  }

  .section {
    padding: 20px 0;
  }

  .section__title {
    text-align: center;
    margin-bottom: 40px;
    font-size: 26px;
  }
}
